import './App.css';

import React from "react";

import SimpleMap from './SimpleMap';
import SimpleMapSidePane from './SimpleMapSidePane';

import SplitPane from 'react-split-pane';

const locations = require("./locations.json");

class App extends React.Component {

	constructor(props) {
		super(props);

		this.refSimpleMapSidePane = React.createRef();

	}

	onMarkerClick(location) {
		if ((undefined !== this.refSimpleMapSidePane) && (undefined !== this.refSimpleMapSidePane.current)) {
			this.refSimpleMapSidePane.current.setLocation(location);
		}
	}

	onClusterClick(cluster) {
		console.log(`onClusterClick`, cluster);
		if ((undefined !== this.refSimpleMapSidePane) && (undefined !== this.refSimpleMapSidePane.current)) {
			this.refSimpleMapSidePane.current.setCluster(cluster);
		}
	}

	render() {
		return (
			<div className="App">
				<SplitPane split="vertical" defaultSize={416}>
					<SimpleMapSidePane ref={this.refSimpleMapSidePane} />
					<SimpleMap locations={locations}
						onMarkerClick={(location) => this.onMarkerClick(location)}
						onClusterClick={(cluster) => this.onClusterClick(cluster)}
					/>
				</SplitPane>
			</div>
		);
	};
}

export default App;