import React from "react";
import './SimpleMapSidePane.css';

import iconDownload from "./download-24.png";
import iconEdit from "./edit-24.png";
import iconTable from "./table-24.png";

import TableMotorStatus from "./TableMotorStatus";
import PhotoGallery from "./PhotoGallery";

class SimpleMapSidePane extends React.Component {

	constructor(props) {
		super(props);

		this.state = {
			location: undefined,
			cluster: undefined
		};
	}

	setLocation = (location) => {
		this.setState({ location: location, cluster: undefined });
	}

	setCluster = (cluster) => {
		this.setState({ cluster: cluster, location: undefined });
	}

	renderLocation(location) {
		return (
			<div className="simple-map-sidebar">
				<img src={process.env.PUBLIC_URL + '/' + location.mainPhoto} alt="mainPhoto" width="400px" />
				<br></br>

				<div className="text id">
					{location.id}
				</div>

				<div className="text">
					{location.Name}
				</div>

				<button>
					<img src={iconEdit} alt="" />
					<div className="button-text">Add/Edit Information</div>
				</button>
				<button>
					<img src={iconDownload} alt="" />
					<div className="button-text">Download Latest Report</div>
				</button>
				<button>
					<img src={iconTable} alt="" />
					<div className="button-text">Show Benchmarking Table</div>
				</button>

				<div className="description text">
					{location.Description}
				</div>

				<img src={process.env.PUBLIC_URL + '/Heatmaps.png'} alt="Heatmaps" width="400px" />

				<TableMotorStatus data={location.Data}></TableMotorStatus>

				<div className="text header">
					Photos:
				</div>

				<PhotoGallery data={location.photos}></PhotoGallery>

			</div>

		);
	}

	renderCluster(cluster) {
		return <div></div>;
	}

	render() {

		if (undefined !== this.state.location) {
			return this.renderLocation(this.state.location);
		}
		else if (undefined !== this.state.cluster) {
			return this.renderCluster(this.state.cluster);
		}
		else {
			return <div></div>;
		}
	};

}

export default SimpleMapSidePane;