import React from "react";
import { Link } from "react-router-dom";
import GoogleMapReact from "google-map-react"
import PropTypes from 'prop-types';
import supercluster from 'points-cluster';
import './SimpleMap.css'

class SimpleMap extends React.Component {

    static defaultProps = {
        onMarkerClick: undefined,
        center: {
            lat: 53.5,
            lng: -2.3
        },
        zoom: 12
    };

    constructor(props) {
        super(props);

        this.state = {
            clusters: []
        };
    }

    onMarkerClick(location) {
        if (undefined !== this.props.onMarkerClick) {
            this.props.onMarkerClick(location);
        }
    }

    onClusterClick(cluster) {
        if (undefined !== this.props.onMarkerClick) {
            this.props.onClusterClick(cluster);
        }
    }

    createClusters = (locations, mapOptions) => {

        var map;

        if (undefined !== mapOptions.bounds) {

            const superclusters = supercluster(locations, {
                minZoom: 0,
                maxZoom: 16,
                radius: 40,
            });

            const clusters = superclusters(mapOptions);

            map = clusters.map(({ wx, wy, numPoints, points }) => ({
                lat: wy,
                lng: wx,
                numPoints,
                id: `${numPoints}_${points[0].id}`,
                points,
            }));
        }
        else {
            map = [];
        }

        this.setState({ clusters: map });
    };

    handleMapChange = ({ center, zoom, bounds }) => {
        this.createClusters(this.props.locations, {
            center,
            zoom,
            bounds,
        });
    };

    renderMarker(item) {
        if (1 === item.numPoints) {
            return <img className='marker-icon' src={process.env.PUBLIC_URL + '/' + item.points[0].icon} alt="pin" onClick={() => this.onMarkerClick(item.points[0])} />;
        }
        else {
            return <div className='marker-group' lat={item.lat} lng={item.lng} onClick={() => this.onClusterClick(item.points[0])}>
                <img className='marker-icon' src={`${process.env.PUBLIC_URL}/${item.points[0].icon}`} alt={item.points[0].icon} />
                <div className="marker-counter">
                    {item.points.length}
                </div>
            </div>;
        }
    }

    render() {
        return (
            // Important! Always set the container height explicitly
            <div style={{ height: "100vh", width: "100%" }}>
                <GoogleMapReact
                    bootstrapURLKeys={{ key: "AIzaSyBeQJKcfrMZkXf3kRJZZkD34V3rnM-IgYI" /* https://console.cloud.google.com/google/maps-apis/credentials?project=hvpdjsoft */ }}
                    defaultCenter={this.props.center}
                    defaultZoom={this.props.zoom}
                    onChange={this.handleMapChange}
                >
                    {
                        this.state.clusters.map(
                            item => { return <Link to={"/"} lat={item.lat} lng={item.lng}>{this.renderMarker(item)}</Link> }
                        )
                    }
                </GoogleMapReact>
            </div>
        );
    }
}

SimpleMap.propTypes = {
    locations: PropTypes.array,
    onMarkerClick: PropTypes.func,
    onClusterClick: PropTypes.func,
    zoom: PropTypes.number,
    center: PropTypes.object
};

export default SimpleMap;